import React, { useState } from 'react'
import Box from './Box'
import Text from './Text'
import BackgroundImage from './BackgroundImage'
import LeftTitle from './LeftTitle'
import PageModule from './PageModule'
import { responsive } from './ThemeProvider/theme'

import circle from './circle.svg'

import useResponsive from '../contexts/mediaQuery/useResponsive'
import Carousel from './Carousel'

const CircleModule = ({ width, top, right, titleText, description, upTitle, ...props}) => (
  <Box.Absolute width={width} top={top} right={right} {...props}>
    <BackgroundImage ratio={1} src={circle} />
    <Box.AbsCenter width="100%" textAlign="center">
      {upTitle && <Text.H4 mb={responsive('1rem', '0.375rem')}>{upTitle}</Text.H4>}
      <Text.H4 whiteSpace="pre-wrap">
        {titleText}
      </Text.H4>
      <Box.Inline>
        {description.map((d, k) => (
          <Text color="circleNoteColor" fontSize={responsive('0.75em', '1em')} mt="0.5rem" whiteSpace="pre-wrap" textAlign="left" key={k}>
            {d}
          </Text>
        ))}
      </Box.Inline>
    </Box.AbsCenter>
  </Box.Absolute>
)

const CirclePageModule = ({
  smallTilte,
  title,
  description,
  note,
  multiple,
  circleData,
  rightMarginTop,
  notes,
}) => {
  const { isMobile } = useResponsive()
  const [ noteIndex, setNote ] = useState(0)
  return (
    <PageModule
      left={
        <LeftTitle
          smallTilte={smallTilte}
          title={title}
          description={description}
          note={!isMobile && note}
          multiple={multiple}
        />
      }
      right={
        <>
          {isMobile ? (
            <Box>
              <Carousel
                width="76%"
                mx="auto"
                slidesToShow={1}
                typeIndex={noteIndex}
                setType={setNote}
                silderProps={{ fade: true }}
              >
                {circleData.map(({ titleText, description, mobileDescription, upTitle }, i) => (
                  <CircleModule
                    titleText={titleText}
                    upTitle={upTitle}
                    description={mobileDescription || description}
                    position="relative"
                    key={i}
                  />
                ))}
              </Carousel>
              {notes && <Text fontSize="0.75em" >{notes[noteIndex]}</Text>}
            </Box>
          ) : circleData.map(({ width, top, right, titleText, description, minWidth, upTitle }, i) => (
            <CircleModule
              width={width}
              minWidth={minWidth}
              upTitle={upTitle}
              top={top}
              right={right}
              titleText={titleText}
              description={description}
              key={i}
            />
          ))}
        </>
      }
      rightMarginTop={rightMarginTop}
    />
  )
}

export default CirclePageModule
